	padding: 0 0 0.6rem 0;
	font-size: 1.6rem;
	font-family: $font_1;
	color: $gold_2;
	background-color: transparent;
	border: none;
	background-clip: padding-box;

	pointer-events: initial;

	&::-webkit-input-placeholder {
		color: $gold_2;
	}
	&::-moz-placeholder,
	&:-moz-placeholder {
		color: $gold_2;
		opacity: 1;
	}
	&:-ms-input-placeholder {
		color: $gold_2;
	}

	/* clears the 'X' from IE */
	&::-ms-clear,
	&::-ms-reveal {
		display: none;
		width: 0;
