@import "ScssHelpers/helpers-all.scss";
@import "product-pairs";

.product_pairs_list {

}

.product_pairs_list__wrap {
	background-color: $beige_1;

	&:not(:last-child) {
		margin-bottom: 4rem;
	}

	&.product_pairs_list__wrap--bg_mod {
		background-color: $gray_3;
	}

	@include bp($point_2, min) {
		padding: 4.8rem 6.4rem;
	}

	@include bp($point_3) {
		padding: 2.4rem 2.4rem 2.8rem;
	}
}

.product_pairs_list__title {
	@include h4;

	letter-spacing: -0.017em;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}
.product_pairs_list__container {
	margin: 0 -2rem -2rem 0;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, flex-start, flex-start);
	}
}

.product_pairs_list__item {
	padding: 0 2rem 2rem 0;

	@include bp($point_2, min) {
		width: 50%;
	}
}

.product_pairs_list__button {
	text-align: center;
}

.btn_1 {
	.product_pairs_list__button & {
		@include bp($point_6 - 1) {
			width: 100%;
		}
	}
}
