@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_color_gold {
	font-weight: 700;
	color: $white;
	background-color: $gold_2;
	border: 1px solid $gold_2;
	transition: background-color 0.3s ease, opacity 0.3s ease;

	@include st_btn;

	&:not(:disabled):hover {
		border-color: $brown_3;
		background-color: $brown_3;
	}

	&:disabled {
		opacity: 0.25;
		pointer-events: none;
	}

	.onboarding_2--active_state & {
		opacity: 1;
		pointer-events: all;
	}
}

@mixin btn_color_black {
	font-weight: 700;
	color: $white;
	background-color: $black;
	transition: opacity 0.3s ease;

	@include st_btn;

	&:hover {
		opacity: 0.7;
	}
}

@mixin btn_color_white {
	font-weight: 500;
	color: $white;
	background-color: $gold_2;
	transition: opacity 0.3s ease;

	@include st_btn;

	&:hover {
		opacity: 0.7;
	}
}

@mixin btn_color_transparent {
