@mixin btn_color_gold_mobile {
	color: $gold_2;
	font-size: 1.5rem;
	line-height: 1.1;
	font-weight: 500;
	letter-spacing: -0.02em;

	border-bottom: 1px solid $gold_2;

	@include st_btn;
}

//size mixins:
@mixin btn_default_size {
	letter-spacing: -0.2px;
	cursor: pointer;

	@include bp($point_2, min) {
		padding: 0 2.2rem;
		height: 4.8rem;
		line-height: 4.6rem;
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		padding: 0 1.6rem;
		height: 4.4rem;
		line-height: 4.4rem;
		font-size: 1.4rem;
	}
}

@mixin btn_wide_size {
	width: 100%;
	height: 6rem;
	padding: 0 2.2rem;

	letter-spacing: 0.13rem;

	line-height: 6rem;
	font-size: 1.8rem;
	text-transform: uppercase;
	cursor: pointer;
}

@mixin btn_icon {
	height: 0.8rem;
	width: 1.6rem;
	margin-left: 1rem;

	color: currentColor;

	.header_sublist & {
		color: $black;
	}
}

