//slider button
@mixin slider_button {
	border-radius: 50%;

	background-color: $gold_2;
	color: $white;

	//transition: opacity 0.3s ease;

	cursor: pointer;
	transition: background-color 0.3s ease, opacity 0.3s ease;

	@include flex_block(row, nowrap, center, center, center);

	&.swiper-button-disabled {
		opacity: 0;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		width: 5.6rem;
		height: 5.6rem;

		&:hover {
			background-color: $brown_3;
		}
	}
	@include bp($point_3) {
		width: 4.4rem;
		height: 4.4rem;
	}
}

@mixin slider_button_big {
	border-radius: 50%;

	background-color: $gold_2;
	color: $white;

	cursor: pointer;
	transition: background-color 0.3s ease, opacity 0.3s ease;

	&:hover {
		background-color: $brown_3;
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		width: 6.4rem;
		height: 6.4rem;
	}

	@include bp($point_3) {
		width: 4.4rem;
		height: 4.4rem;
	}
}

@mixin slider_button_bordered {
	border-radius: 50%;

	background-color: transparent;
	border: 1px solid $gold_2;
	color: $gold_2;

	transition: background-color 0.3s ease, color 0.3s ease;
	cursor: pointer;

	&.hero_slider__button {
		color: $white;
		border-color: $white;
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		width: 4.8rem;
		height: 4.8rem;

		&:hover {
			border-color: $brown_3;
			background-color: $brown_3;
			color: $white;
		}
	}

	@include bp($point_3) {
		width: 3.6rem;
		height: 3.6rem;
	}
}

@mixin spoiler_acc($fz: 1.6rem) {
	position: relative;
	font-weight: 500;
	letter-spacing: -0.03em;
	line-height: 1.2;
	color: $gold_2;
	cursor: pointer;

	&:before {
		position: absolute;
		left: 0;
		bottom: -2px;

		display: block;
		width: 100%;
		background-color: $gold_2;
		transition: opacity 0.3s ease;

		content: "";
	}

	@include bp($point_2, min) {
		font-size: $fz;
		&:before {
			height: 2px;
		}
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		&:before {
			height: 1px;
		}
	}

	&:focus,
	&:hover {
		&:before {
			opacity: 0;
		}
	}
}

@mixin spoiler_acc_icon {
	width: 1.2rem;
	height: 1.2rem;
	flex-shrink: 0;

	@include transition_transform;
}
