@import "ScssHelpers/helpers-all.scss";

.checkout_coupon {

}

.coupon {
	display: flex;
}

.coupon .input-text {
	@include form_el_default;
	padding: 0rem 4.5rem 0.3rem 1.9rem;
	max-width: 33.4rem;

	box-shadow: inset 0 0 0 1px #bb753a;

	&:focus {
		&::placeholder {
			color: transparent;
		}
	}
}

.coupon .button {
	@include btn_default_size;
	@include btn_color_gold;

	margin: 0;
}