@import "ScssHelpers/helpers-all.scss";

.cart_content {
	@include bp($point_2, min) {
		padding-top: 5.8rem;
	}
}

.cart_content .woocommerce {
	&:not(:last-child) {
		margin-bottom: 6.7rem;
	}

	@include bp($point_2, min) {
		display: flex;
	}
}

.cart_content__col {
	@include bp($point_2, min) {
		width: 47.2%;		
	}

	@include bp($point_3) {
		width: 100%;		
	}
}

.cart_content .woocommerce-row {
	@include bp($point_2, min) {
		width: 97.6%;
		margin-right: 2rem;
	}

	@include bp($point_3) {
		width: 100%;
	}

	@include bp($point_3) {
		margin-bottom: 1.5rem;
	}


}

.cart_content .woocommerce-cart-form {

	border-bottom: 1px solid #f2e4d1;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_4, min) {
		padding-bottom: 4rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 2rem;
	}
}

.cart_content__title {
	@include h3;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.cart_content__text {
	@include text;
	max-width: 94rem;
	line-height: 1.48;

	&:not(:last-child) {
		margin-bottom: 4.2rem;
	}
}

.cart_content .woocommerce-cart-form {
	width: 100%;

	@include bp($point_2, min) {
		max-width: 78.2rem;
	}
}

.cart_content table {
	width: 100%;
}

.cart_content thead {
	border-bottom: 1px solid $beige_2;
}

.cart_content thead tr th {
	padding-bottom: 1.2rem;
	
	&:nth-child(2),
	&:nth-child(4) {
		@include bp($point_5 - 1) {
			display: none;
		}
	}
}

.product-name,
.product-price,
.product-quantity,
.product-subtotal {
	.cart_content thead & {
		padding-right: 1rem;
		font-size: 1.6rem;
		font-weight: 500;
		letter-spacing: 0.09em;
		line-height: 1.5;
		text-transform: uppercase;

		color: $gray_2;
	}
}

.cart_content .cart_item td {

	&:not(:last-child) {
		padding-right: 1rem;
	}
}

.cart_content .cart_item:first-child td {
	padding-top: 2.3rem;
	padding-bottom: 0.5rem;
}

.cart_content .cart_item:not(:first-child) td {
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
}

.cart_content .cart_item td:nth-child(2) {

	@include bp($point_2, min) {
		padding-left: 1.6rem;
	}

	@include bp($point_3) {
		padding-left: .5rem;
	}
}

.product-thumbnail {
	.cart_content .cart_item & {
		min-width: 8rem;
		width: 8rem;
	}
	
	a {
		position: relative;

		display: block;
		width: 100%;
		height: 100%;

		&:before {
			content: '';
			@include stretch;
			@include transition_opacity;

			background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .7));
			opacity: 0;
		}

		&:hover {
			&::before {
				opacity: 1;
			}
		}

		& img {
			@include cover_img;
		}
	}
}

.product-name {
	.cart_content thead & {
		text-align: left;
	}

	.cart_content .cart_item & {
		@include bp($point_2, min) {
			width: 37.3%;
		}

		@include bp($point_3, $point_5) {
			width: 44%;
		}

		@include bp($point_5 - 1) {
			width: auto;
		}
	}

	a {
		.cart_content .cart_item & {
			position: relative;

			font-weight: 500;
			letter-spacing: -0.012em;
			text-align: left;
			color: $black;

			@include bp($point_4, min) {
				font-size: 1.8rem;
			}
	
			@include bp($point_4 - 1) {
				font-size: 1.5rem;
			}
		}
	}
}

.product-price {
	.cart_content .cart_item & {
		@include bp($point_2, min) {
			width: 16.8%;
		}

		@include bp($point_3) {
			width: 10%;
		}

		@include bp($point_5 - 1) {
			display: none;
		}
	}
}

.product-quantity {
	.cart_content .cart_item & {
		width: 19.8%;
		padding-left: 0.5rem;
		padding-right: 0.5rem;

		& .quantity {
			width: 100%;
			height: max-content;
		}

		& .input_number {
			@include bp($point_6 - 1) {
				font-size: 1.6rem;
			}
		}
	}

	@include bp($point_5 - 1) {
		width: 30%;
	}
}

.product-price,
.product-subtotal {
	.cart_content .cart_item & {
		padding-bottom: 1.2rem;

		font-weight: 400;
		text-align: center;

		color: $black;

		bdi {
			letter-spacing: 0.01em;
		}

		@include bp($point_4, min) {
			font-size: 1.8rem;
		}

		@include bp($point_4 - 1) {
			font-size: 1.5rem;
		}

		@include bp($point_5 - 1) {
			display: none;
		}
	}
}

.product-subtotal {
	.cart_content .cart_item & {
		width: 13.5%;
	}
}

.product-remove {
	@include bp($point_3) {
		width: 5%;
	}
}
.product-remove a {
	.cart_content .cart_item & {
		@include transition_color;

		display: block;
    text-align: center;

		font-size: 3rem;

		color: $gold_2;

		&:hover,
		&:focus {
			text-decoration: none;
			color: darken($gold_2, 10);
		}
	}
}

.cart_content tbody tr .actions {
	padding-top: 1.5rem;
}

.cart_content .actions .button-continue-shopping {
	@include btn_default_size;
	@include btn_color_transparent;


	@include bp($point_6 - 1, min) {
		float: left;
	}

	@include bp($point_6) {
		width: 100%;

		margin-bottom: 1rem;
	}
}

.cart_content .actions button {
	@include btn_default_size;
	@include btn_color_gold;

	&:disabled {
		opacity: 0.25;
	}

	@include bp($point_6 - 1, min) {
		float: right;
	}

	@include bp($point_6) {
		width: 100%;
	}
}
