@import "ScssHelpers/helpers-all.scss";

.product_pairs {
	display: flex;
	justify-content: flex-start;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		align-items: stretch;
	}

	@include bp($point_3) {
		align-items: center;
		align-content: center;
	}
}

.product_pairs__image {
	position: relative;

	flex-shrink: 0;

	overflow: hidden;

	&::after {
		content: "";
		display: block;
		width: 100%;
		padding-bottom: 100%;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 3.9rem;
		}

		@include bp($point_3) {
			margin-right: 1.9rem;
		}
	}

	.featured_recipe & {

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-right: 3.2rem;
			}

			@include bp($point_3) {
				margin-right: 2rem;
			}
		}

		@include bp($point_2, min) {
			width: 16rem;
		}

		@include bp($point_3) {
			width: 42%;
		}
	}

	.cuts_info__post_list_item & {
		width: 16rem;

		&:not(:last-child) {
			margin-right: 3rem;
		}
	}

	&:hover,
	&:focus {
		.product_pairs_img {
			scale: 1.06;
		}
	}

	@include bp($point_2, min) {
		width: 20.8rem;
	}
	@include bp($point_3) {
		width: 43%;
		min-height: 12rem;
		max-height: 18rem;
	}
}

.product_pairs_img {
	@include stretch;

	transition: scale 0.3s ease;
}

.product_pairs_img__in {
	@include cover_img;
}

.product_pairs__content {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
}

.product_pairs__title {
	@include h4;

	max-width: 29.8rem;

	color: $black;
	letter-spacing: -0.018em;
	line-height: 1.2;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	.featured_recipe & {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_4, min) {
			font-size: 2.4rem;
		}
	}

	.cuts_info__post_list_item & {
		letter-spacing: -0.055em;
		line-height: 1.08;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
		
		@include bp($point_2, min) {
			font-size: 2.6rem;
		}
	}
}

.product_pairs__title a {
	color: $black;

	transition: color 0.3s;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $brown_2;
	}
}

.product_pairs__rating {
	display: flex;
	align-items: center;

	color: $gold_1;

	.cuts_info__post_list_item & {
		font-size: 1.4rem;
		letter-spacing: -0.018em;

		span {
			padding-left: 0.8rem;
		}
	}
	&:focus,
	&:hover {
		text-decoration: none;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_3) {
		order: 3;

		img {
			width: 50%;
		}
	}
}

.product_pairs__price {
	display: flex;
	align-items: center;

	color: $black;
	font-weight: 500;
	letter-spacing: 0.046em;
	line-height: 1;
	text-align: left;
	text-transform: uppercase;

	.featured_recipe & {
		@include bp($point_4, min) {
			font-size: 2rem;
		}
	}

	.cuts_info__post_list_item & {
		font-size: 2rem;
		letter-spacing: 0.052em;
	}

	@include bp($point_2, min) {
		font-size: 2.2rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.6rem;

		@include bp($point_3) {
			margin-bottom: .6rem;
		}
	}
}

.product_pairs__old_price {
	font-weight: 300;
	color: $gray_1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 1.2rem;
		}

		@include bp($point_3) {
			margin-right: .2rem;
		}
	}
}

.product_pairs__regular_price {
}

.product_pairs__buttons {
	// margin-top: auto;
	@include bp($point_3) {
		order: 4;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}
